@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,400;0,500;0,600;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600&display=swap);
@import url(https://fonts.cdnfonts.com/css/mistrully);
/* Global Styles  */

html{

    --font-size-sm: 0.75rem;   /* 12px */
    --font-size-base: 1rem;    /* 16px, base */
    --font-size-md: 1.25rem;   /* 20px */
    --font-size-lg: 1.5rem;    /* 24px */
    --font-size-xl: 1.75rem;   /* 28px */
    --font-size-xxl: 2rem;     /* 32px */
    --font-size-xxxl: 2.25rem; /* 36px */

    --font-weight-xs: 400;
    --font-weight-md: 500;
    --font-weight-lg: 600;
    --font-weight-xlg: 700;

    --margin-large: 5% 10% 5% 10%;
    --margin-small: 5% 0% 5% 0%;
    --margin-sm: 5vh 0% 5vh 0%;

    --padding-small: 4% 4% 4% 4%;


    --tm-blue: #004165;
    --tm-red: #772432; 
    --tm-grey:  #A9B2B1;
    --tm-yellow: #F2DF74;
    --tm-blue-grad: linear-gradient(90deg, rgba(3,41,62,1) 4%, #004165 50%);
    --tm-join-green: #298e5a;
    --tm-enq-orange: rgb(255, 132, 0);
    /* --tm-blue-grad:
    --tm-red-grad: */

    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', 'Montserrat', 'Mistrully' 'Source Sans Pro' sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    margin: 0;
    width: 100%;
    overflow: scroll;
}


/* Text  */
.header-text-lg{
    font-size: 2.25rem;
    font-size: var(--font-size-xxxl);
    font-family: Montserrat;
    font-weight: 700;
    font-weight: var(--font-weight-xlg);

}

.header-text-md{
    font-size: 2rem;
    font-size: var(--font-size-xxl);
    font-family: Montserrat;
    font-weight: 600;
    font-weight: var(--font-weight-lg);

}

.header-text-mdlg{
    font-size: 1.75rem;
    font-size: var(--font-size-xl);
    font-family: Montserrat;
    font-weight: 600;
    font-weight: var(--font-weight-lg);

}

.header-text-sm{
    font-size: 1.5rem;
    font-size: var(--font-size-lg);
    font-family: Montserrat;
    font-weight: 600;
    font-weight: var(--font-weight-lg);

}

.accent-text{
        font-family: 'Mistrully', sans-serif;
        font-weight: 500;
        font-weight: var(--font-weight-md);
        font-size: 1.5rem;
        font-size: var(--font-size-lg);
        letter-spacing: 2px;

}

.body-text-xlg{
    font-size: 2.25rem;
    font-size: var(--font-size-xxxl);
    font-weight: 600;
    font-weight: var(--font-weight-lg);
    font-family: Source Sans Pro;
}

.body-text-lg{
    font-size: 1.25rem;
    font-size: var(--font-size-md);
    font-weight: 500;
    font-weight: var(--font-weight-md);
    font-family: Source Sans Pro;
}

.body-text-lg-bold{
    font-weight: 600;
    font-weight: var(--font-weight-lg);
}


.body-text-sm{
    font-size: 1rem;
    font-size: var(--font-size-base);
    font-family: Source Sans Pro; 
}

.link-text{
    font-family: Source Sans Pro;
    font-weight: 400;
    font-weight: var(--font-weight-xs);
    list-style-type: none;

}
.link-text-lg{
    font-size: 1.25rem;
    font-size: var(--font-size-md);
    font-family: Source Sans Pro;
    font-weight: 400;
    font-weight: var(--font-weight-xs);
    list-style-type: none;

}

.link-text-sm{
    font-size: 0.75rem;
    font-size: var(--font-size-sm);
    font-family: Source Sans Pro;
    font-weight: 400;
    font-weight: var(--font-weight-xs);
    list-style-type: none;

}

.block-quote-quote{
    font-size: 2.25rem;
    font-size: var(--font-size-xxxl);
    font-family: Source Sans Pro;

}

.block-quote-auth{
    font-style: italic;
    font-family: Source Sans Pro;

}

/* Sections  */

.margin-section-lg{
    margin: 5% 10% 5% 10%;
    margin: var(--margin-large);
}

.margin-section-sm{
    margin: 5% 0% 5% 0%;
    margin: var(--margin-small);
}

.margin-top-sm{
    margin: 5vh 0% 5vh 0%;
    margin: var(--margin-sm);
}


/*  Divide by 2  */

.section-two{

}

/*  Divide by 3  */

.section-three{

}

/*  Divide by 4  */

.section-four{

}

.flex-row{
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.flex-wrap{
    flex-wrap: wrap;

}

.flex-gap{
    grid-gap: 50px;
    gap: 50px;
    
}

/* Positioning */

.text-centre{
    text-align: center;
}

.vertical-center {
        margin: 0;
        position: absolute;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
    
}

/* buttons */

.join-button-small{
    background-color: #298e5a;
    background-color: var(--tm-join-green);
    font-size: 1.25rem;
    font-size: var(--font-size-md);
    font-weight: 600;
    font-weight: var(--font-weight-lg);
    padding: 4% 4% 4% 4%;
    padding: var(--padding-small);
    color: rgb(255, 255, 255);
    border-radius: 12px;
    height: auto;
    width: 200px;
    border: transparent;
    text-align: center;
    text-decoration: none;
    display: inline-block;

    margin: 4px 2px;
    cursor: pointer;
    font-weight: 600;
    font-weight: var(--font-weight-lg);

}

.join-button-big{
    background-color: #298e5a;
    background-color: var(--tm-join-green);
    font-size: 1.75rem;
    font-size: var(--font-size-xl);
    font-weight: 600;
    font-weight: var(--font-weight-lg);

    color: rgb(255, 255, 255);
    border-radius: 12px;
    height: 100px;
    width: 300px;
    border: transparent;
    text-align: center;
    text-decoration: none;
    display: inline-block;

    margin: 4px 2px;
    cursor: pointer;
  
}

.query-button{
    background-color: rgb(255, 132, 0);
    background-color: var(--tm-enq-orange);
    font-size: 1.25rem;
    font-size: var(--font-size-md);
    font-weight: 600;
    font-weight: var(--font-weight-lg);

    color: rgb(255, 255, 255);
    border-radius: 12px;
    height: 55px;
    width: 200px;

    border: transparent;
    text-align: center;
    text-decoration: none;
    display: inline-block;

    margin: 4px 2px;
    cursor: pointer;

}

/* hover styles */

a {
    text-decoration: none;
    color: #ffffff;
}

a:hover {
    text-decoration: underline;
    color: #ffffff;
}

a:checked {
    text-decoration: none;
    color: #ffffff;
}


/* Media */

 .navbar {
    position: -webkit-sticky;
    position: sticky;
    display: flex;
    height: 95px;
    padding: 1%;
    flex-direction: row;
    max-width: 100vw;
    background:var( --tm-blue-grad);
    box-shadow: 0px 1px 5px #888, 0px 0px 5px #a9b2b1;
    color: #ffffff;
    vertical-align: baseline;
    z-index: 3;
    right: 0;
    left: 0;
    top: 0;
}


.header-info {
    width: auto;
    display: flex;
    text-align: center;
    flex-direction: row;
    justify-content: center;
    font-family: Source Sans Pro;
    font-weight: 600;
    font-size: 1.25rem;
}
.button {
    border-radius: 25px;
    background: #4bb543;
    padding: 20px;
    width: 180px;
    height: 25px;
    margin: 1;
    float: inline-end;
}

.button li {
    text-decoration: none;
    color: white;
    font-family: Source Sans Pro;
    font-weight: 600;
    font-size: 1.25rem;
}

.navbar-menu {
    /* margin-top: px; */
    top: 50%;
    position: absolute;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    right: 0;
    z-index: 3;
}

.header-menu li {
    display: flex;
    flex-direction: row;
    display: inline-flex;
    padding-left: 20px;
    padding-right: 20px;
    font-family: Source Sans Pro;
    font-weight: 400;
    font-size: 1.25rem;
}

@media only screen and (max-width: 950px) {
    .header :nth-child(1) {
        order: 1;
    }
    .header :nth-child(2) {
        order: 2;
        align-content: flex-end;
    }

    .typography {
        margin-top: auto;
        margin-bottom: auto;
    }
    .header-image {
        width: 20vw;
        height: auto;
        display: block;
        justify-content: center;
        align-items: center;
        object-fit: contain;
    }
}

.modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    outline: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 2;
}

.modal-background {
    position: fixed;
    top: 95px;
    right: 0;
    z-index: 00;
    background: #772432;
    float: right;
    z-index: 100;
}

.modal-body {
    position: relative;
    right: 0;
    bottom: 0;
    top: 0;
    left: 0;
    width: 50vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

.modal-menu-content {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    max-height: 30rem;
    overflow-x: hidden;
    overflow-y: auto;
    font-size: 2.5rem;
    text-decoration: none;
}

.modal-menu-content li {
    list-style: none;
    text-decoration: none;
    margin-top: 40px;
    margin-bottom: 40px;
}


.navbar-logo {
    display: inline-flex;
}

.logo-image {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 90px;
}
.ptm-logo-text {
    width: auto;
    height: 100px;
    display: block;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.typography {
    font-family: 'Montserrat', sans-serif;    
    font-weight: 600;
    font-size: 1.75rem;
    margin: 1%;
    margin-top: auto;
    margin-bottom: auto;

}

@media only screen and (max-width: 600px) {
    .hidden {
        display: none;
    }
}
.footer {
    position: absolute;
    width: 100vw;
    height: auto;
    background:var( --tm-blue-grad);
    box-shadow: 0px 1px 5px #888, 0px -10px 5px #a9b2b1;
    margin-top: 2%;
    color: #ffffff;
    display: flex;
    flex-direction: row;
    padding-top: 2%;
    padding-bottom: 2%;
    padding-right: auto;
    padding-left: auto;
    font-family: Source Sans Pro;
}

.footerImage {
    width: auto;
    height: 150px;
    margin-left: auto;
    margin-right: auto;
    display: block;
}


.footer ul, li {
    margin-left: 0px;
    padding: 0px;
    list-style-type: none;
}

.tagline{
    width: 100%;
}


.footerSection  {
    display: flex;
    flex-direction: column;
    max-width: 40vw;
    flex-shrink: 5;
    flex-grow: 1;
}

.usefulLinks{
    align-items: flex-end;
    text-align: right;
    flex-direction: column;
    list-style-type: none;
    padding: 0px;
    margin: 15px 0;
}

.centreInDiv {
    margin-top: 10px;
    display: block;
    text-align: center;
}

.centre{
    align-items: center;
}

.button {
    color: rgb(19, 19, 19);
}



@media only screen and (max-width: 1000px) {
    .footer {
        position: absolute;
        width: 100vw;
        height: auto;
        background-color: #004165;
        box-shadow: 0px 1px 5px #888, 0px -10px 5px #a9b2b1;
        margin-top: 2%;
        color: #ffffff;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;

    }

    .usefulLinks {
        text-align: center;
        list-style-type: none;
      }


    .mobile-footerSection {
        /* On small screens, we are no longer using row direction but column */
        margin-left: 30px;
        margin-right: 30px;
        text-align: left;
        min-width: 200px;
        display: inline-block;
        justify-content: space-evenly;
        /* how much space taken flexing */
    }

    .footerImage {
        max-width: 200px;
        height: auto;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        display: block;
    }

    .tagline{
        max-width: 600px;
    }
    .footerSection {
        display: inline-block;
        justify-content: space-around;
        vertical-align: top;
        max-width: 100vw;
        align-items: center;
    }

    .footerPTMDetails {
        margin-left: auto;
        margin-right: auto;
    }

    .footerPTMDetails h4,
    h5,
    p {
        margin: 5px;
    }

    .footerPTMDetails p {
        font-size: 14px;
    }

    .footerPTMDetails h3 {
        font-size: 16px;
    }

    .vertical-center {
        margin: 0;
        position: absolute;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
    }

}

